@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body::-webkit-scrollbar {
  width: 5px;
}

body::-webkit-scrollbar-thumb {
  background: #fff;
}

body::-webkit-scrollbar-track {
  background: #20b422;
}

/* blue colot : #48bfe3      */

.App {
  margin: 0;
  text-align: center;
  display: block;
  height: 200vh;
  width: auto;
  color: white;
  background-color: #20b422;
}

.password-generator {
  margin-top: 70px;
}

.password-output {
  width: 80%;
  height: 100px;
  font-size: 40px;
  text-align: left;
  border-bottom: 2px solid white;
  margin-left: auto;
  margin-right: auto;
}

.password-output-span {
  color: white;
}

.generate-button {
  background: url("https://d38muu3h4xeqr1.cloudfront.net/website/static/DL-5655/images/icons/icon-passwordgenerator-large-white.svg")
    no-repeat;
  text-indent: -9999px;

  overflow: hidden;
  align-self: center;
  min-width: 60px;
  min-height: 60px;
  float: right;
  margin-top: 10px;
  border: none;
}

.generate-button:active {
  border-style: outset;
  border: none;
}

.toggle-div {
  margin-top: 10vh;
}

.slider {
  width: 98vw;
}

.length-slider {
  width: 80%;
  margin-top: 20vh;
  height: 100px;
}

.length-slider {
  -webkit-appearance: none;
  width: 80%;
  height: 10px;
  border-radius: 30px !important;
  background-color: #79d27a;
  outline: none;
}

.length-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: white;
  cursor: pointer;
}

.length-slider::-moz-range-thumb {
  width: 25px;
  height: 10px;
  border-radius: 50%;
  background: white;
  cursor: pointer;
}

.pagetitle {
  font-size: 60px;
  font-family: "Montserrat", sans-serif;
  padding-top: 60px;
}

.type-toggle {
  display: none;
}

.toggle-box {
  display: flex;
  justify-content: space-evenly;
  flex-direction: row;
}

.check {
  cursor: pointer;
  position: relative;
  margin: auto;
  width: 18px;
  height: 18px;
  -webkit-tap-highlight-color: transparent;
  transform: translate3d(0, 0, 0);
}
.check:before {
  content: "";
  position: absolute;
  top: -15px;
  left: -15px;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background: rgba(34, 50, 84, 0.03);
  opacity: 0;
  transition: opacity 0.2s ease;
}
.check svg {
  position: relative;
  z-index: 1;
  fill: none;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke: white;
  stroke-width: 1.5;
  transform: translate3d(0, 0, 0);
  transition: all 0.2s ease;
}
.check svg path {
  stroke-dasharray: 60;
  stroke-dashoffset: 0;
}
.check svg polyline {
  stroke-dasharray: 22;
  stroke-dashoffset: 66;
}
.check:hover:before {
  opacity: 1;
}
.check:hover svg {
  stroke: #fff;
}
#t1:checked + .check svg {
  stroke: #fff;
}
#t1:checked + .check svg path {
  stroke-dashoffset: 60;
  transition: all 0.3s linear;
}
#t1:checked + .check svg polyline {
  stroke-dashoffset: 42;
  transition: all 0.2s linear;
  transition-delay: 0.15s;
}

#t3:checked + .check svg {
  stroke: #fff;
}
#t3:checked + .check svg path {
  stroke-dashoffset: 60;
  transition: all 0.3s linear;
}
#t3:checked + .check svg polyline {
  stroke-dashoffset: 42;
  transition: all 0.2s linear;
  transition-delay: 0.15s;
}

#t2:checked + .check svg {
  stroke: #fff;
}
#t2:checked + .check svg path {
  stroke-dashoffset: 60;
  transition: all 0.3s linear;
}
#t2:checked + .check svg polyline {
  stroke-dashoffset: 42;
  transition: all 0.2s linear;
  transition-delay: 0.15s;
}

.toggle-labels {
  font-size: 30px;
  margin-left: 20px;
}

